
.VP_container {
    padding: 80px 20% 0;
    /* padding: 0 ; */
}

.VP_title {
    font-size: 40px;
    margin: 0 0 20px; 
    text-align: center;
    color: #007BFF;
}

.visualize_type {
    font-size: 30px;
    margin: 20px 0;
}

.visualize_list {
    display: flex;
    width: 100%;
    margin: 0;
}

.visualize_item {
    width: 300px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    overflow: hidden;
    margin: 0 20px 0 0;
}

.visualize_item a {
    display: block;
    width: 100%;
    height: 100%;
    position: relative;
}

.visualize_item a .name {
    position: absolute;
    display: grid;
    place-content: center;
    top: 0;
    width: 100%;
    height: 100%;
    font-size: 20px;
    transition: all .1s linear;
    background-color: rgba(0, 123, 255, .6);
    opacity: 0;
    color: #fff;
    font-weight: bold;
    font-family: sans-serif;
}

.visualize_item a:hover > .name {
    opacity: 1;
}

.visualize_image {
    height: 100%;
    width: 100%;
    object-fit: cover;
}